import React from 'react';
import { Page, Container, UploadImage, Grid, Input } from '../../components';
import bridge from "@vkontakte/vk-bridge";

export default function ProfilePage(): React.ReactElement {
  const [user, setUser] = React.useState(null);

  React.useEffect(() => {
    bridge.subscribe(({ detail: { type, data }}) => {
      if (type === 'VKWebAppUpdateConfig') {
        const schemeAttribute = document.createAttribute('scheme');
        // @ts-ignore
        schemeAttribute.value = data.scheme ? data.scheme : 'client_light';
        document.body.attributes.setNamedItem(schemeAttribute);
      }
    });
    async function fetchData() {
      const user = await bridge.send('VKWebAppGetUserInfo');
      // @ts-ignore
      setUser(user);
    }
    fetchData();
  }, []);

  // @ts-ignore
  const userName = user && user.first_name;

  return (
    <Page title="–ü—Ä–æ—Ñ–∏–ª—å" layout="profile">
      {[
        {
          slug: 'profile',
          title: '–ü—Ä–æ—Ñ–∏–ª—å',
          component: (
            <Container>
              <UploadImage url="http://fetbiko.ru/public/img/avatar.png" />
              <Grid>
                <Input label="–ò–º—è" defaultValue={userName || undefined} />
                <Input label="–û–ø–∏—Å–∞–Ω–∏–µ" defaultValue="Product Designer at Linqed.me" />
                <Input label="–ê–¥—Ä–µ—Å —Å—Ç—Ä–∞–Ω–∏—Ü—ã" defaultValue="linqed.me/fetchlab" />
              </Grid>
            </Container>
          )
        },
      ]}
    </Page>
  );
}